<template>
	<div class="project-page">
		<div class="card" v-for="project in arrayProject">
			<el-carousel height="400px">
				<el-carousel-item v-for="item in project.imgs" :key="item">
					<img :src="item" />
				</el-carousel-item>
			</el-carousel>
			<div class="card-content" style="text-align: left;margin:5px">
				<p style="margin-top: 10px;">项目名称：</p>
				<p>{{project.projectName}}</p>
				<p> 项目描述：</p>
				<p>{{project.projectDes}}</p>
				<p>主要技术：</p>
				<p v-for="obj in project.projectContent">{{obj}}</p>
				<p>主要技术：</p>
				<p v-for="obj in project.projectTechnology">{{obj}}</p>
			</div>
		</div>
	</div>
	</div>
</template>
<script>
	export default {
		name: 'hello',
		data() {
			return {
				msg: 'Welcome to Your Vue.js App',
				imgs: ['/static/img/boka1.jpg', '/static/img/1.jpg', '/static/img/liang10.jpg'],
				arrayProject: []
			}
		},
		// 自定义方法
		methods: {
			//去list 页面
			GoToList: function() {
				sendEvent('aaab', 'fangchungao');
			}
		},
		// 生命周期方法 
		mounted: function() {
			var that = this;
			$.ajax({
				type: "get",
				url: "../../static/data.json",
				success: function(data) {
					that.arrayProject = data.ios;
					if(that.$route.params.id == 'h5') {
						that.arrayProject = data.h5;
					}
					if(that.$route.params.id == 'ios') {
						that.arrayProject = data.ios;
					}
				}
			})
			this.$on('test', function(msg) {
					alert(msg);
				}),
				catchEvent('aaab', function(event) {
					//				alert(888);
					alert(event.info);
				});
			//监听消息
			addEvent('abcd', function(event) {
				//				alert(123);
			})
		},
	}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
	.card {
		max-width: 800px;
		box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
		text-align: left;
		overflow: hidden;
		margin: 10px auto;
	}
	
	.card-img {
		text-align: center;
	}
	
	.card-content {
		padding: 10px;
		border: 1px solid #D3DCE6;
		border-radius: 5px;
	}
	
	.el-carousel__item h3 {
		color: #475669;
		font-size: 14px;
		opacity: 0.75;
		line-height: 150px;
		margin: 0;
	}
	
	.el-carousel__item:nth-child(2n) {
		background-color: #99a9bf;
	}
	
	.el-carousel__item:nth-child(2n+1) {
		background-color: #d3dce6;
	}
</style>


